
/* -------------------------------------
 * Bar container
 * ------------------------------------- */
.css-bar {
  position: relative;
  display: inline-block;
  font-size: 16px;
  border-radius: 50%;
  background-color: transparent;
  margin-bottom: 20px;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  width: 80px;
  height: 80px;
  font-size: 18px;
}
.css-bar:after,
.css-bar > img {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  text-align: center;
  font-weight: light;
  color: #a1a2a3;
}
.css-bar:after {
  content: attr(data-label);
  background-color: #fff;
  z-index: 101;
}
.css-bar > img {
  z-index: 102;
}
.css-bar:after,
.css-bar > img {
  width: 70px;
  height: 70px;
  margin-left: 5px;
  margin-top: 5px;
  line-height: 70px;
}

